
import React, { Component } from 'react';

import './App.css';
import Profile from './Profile';

class App extends Component {
  constructor(){
    super();

    this.state= {
      show:true,

    }
    }

    togel=()=>{
      this.setState({show: !this.state.show})
    }
    
      render() {
        return (
          <div>
    <button   onClick={()=>this.togel()} >show </button>
        {this.state.show   &&     <Profile/>}
          </div>
      )};
    

  

}

export default App;



